export const formatDate = ({ dateFromBase, type }) => {
    
    const date = new Date();
    const dt = new Date(dateFromBase);

    const dayMonthDate = ((date.getDate()) + '').padStart(2, "0");
    const dayMonthDt = ((dt.getDate()) + '').padStart(2, "0");

    const monthDate = ((date.getMonth() + 1) + '').padStart(2, "0");
    const monthDt = ((dt.getMonth() + 1) + '').padStart(2, "0");

    const yearDate = date.getFullYear();
    const yearDt = dt.getFullYear();

    const hours = (dt.getHours() + '').padStart(2, "0");
    const minutes = (dt.getMinutes() + '').padStart(2, '0');

    const returnDate = () => {
        switch(type) {
            case 'dialogs': 
                return `${dayMonthDt}.${monthDt}.${yearDt}`;
            case 'messages':
                return `${dayMonthDt}.${monthDt}.${yearDt} - ${hours}:${minutes}`;
            default:
                return `${dayMonthDt}.${monthDt}.${yearDt} - ${hours}:${minutes}`;
                
        }
    };

    if(dayMonthDate !== dayMonthDt) { //ÐµÑÐ»Ð¸ Ð´Ð½Ð¸ Ð¼ÐµÑÑÑÐ° Ð½Ðµ ÑÐ¾Ð²Ð¿Ð°Ð´Ð°ÑÑ
        if((dayMonthDate - dayMonthDt) === 1) { // ÐµÑÐ»Ð¸ ÑÐ¶Ðµ Ð½Ð°ÑÑÑÐ¿Ð¸Ð» ÑÐ»ÐµÐ´ÑÑÑÐ¸Ð¹ Ð´ÐµÐ½Ñ
            if(monthDate === monthDt && yearDate === yearDt) { //ÐµÑÐ»Ð¸ Ð¼ÐµÑÑÑÐ° Ð¸ Ð³Ð¾Ð´Ð° ÑÐ¾Ð²Ð¿Ð°Ð´Ð°ÑÑ
                if(type === 'dialogs') { //ÐµÑÐ»Ð¸ Ð´Ð¸Ð°Ð»Ð¾Ð³
                    return 'ÐÑÐµÑÐ°'; //ÑÐ¾ Ð²ÐµÑÐ½Ð¸ "ÐÑÐµÑÐ°"
                } else return `ÐÑÐµÑÐ° - ${hours}:${minutes}`; //Ð¸Ð½Ð°ÑÐµ Ð²ÐµÑÐ½Ð¸ "Ð²ÑÐµÑÐ°" Ð¸ Ð²ÑÐµÐ¼Ñ
            } else return returnDate(); //Ð¸Ð»Ð¸ ÐµÑÐ»Ð¸ Ð¼ÐµÑÑÑ Ð¸Ð»Ð¸ Ð³Ð¾Ð´ Ð½Ðµ ÑÐ¾Ð²Ð¿Ð°Ð´Ð°ÑÑ, ÑÐ¾ Ð²ÐµÑÐ½Ð¸ Ð¿ÑÐ¾ÑÑÐ¾ Ð´Ð°ÑÑ
        } else return returnDate();  //Ð¸Ð»Ð¸ ÐµÑÐ»Ð¸ Ð½Ð°ÑÑÑÐ¿Ð¸Ð» ÑÐ¶Ðµ Ð±Ð¾Ð»ÑÑÐµ, ÑÐµÐ¼ Ð¾Ð´Ð¸Ð½ Ð´ÐµÐ½Ñ
    } else { //Ð½Ð¾ ÐµÑÐ»Ð¸ ÑÐ¾Ð²Ð¿Ð°Ð´Ð°ÐµÑ Ð´ÐµÐ½Ñ
        if(monthDate !== monthDt || yearDate !== yearDt) { //ÐµÑÐ»Ð¸ Ð¸Ð»Ð¸ Ð¼ÐµÑÑÑ, Ð¸Ð»Ð¸ Ð³Ð¾Ð´ Ð½Ðµ ÑÐ¾Ð²Ð¿Ð°Ð´Ð°ÑÑ
            return returnDate();  //Ð²Ð¾Ð·Ð²ÑÐ°ÑÐ°ÐµÐ¼ Ð´Ð°ÑÑ
        } else { //ÐµÑÐ»Ð¸ ÑÐ¾Ð²Ð¿Ð°Ð´Ð°ÐµÑ Ð°Ð±ÑÐ¾Ð»ÑÑÐ½Ð¾ Ð²ÑÐµ
            return `${hours}:${minutes}`;
        }
    }

};